/* common */
html, body {
  width: 100%;
  height: 100%;
  background: #ffffff;
  margin: 0;
  padding: 0;
  color: #616161;
  font-size: 14px;
  line-height: 1;
  font-family: 'Montserrat', sans-serif;
  overflow-x: hidden;
  transition: all 0.2s ease-in;
}

body {
  min-width: 320px;
}

a {
  color: #ee8467;
  text-decoration: none;
  transition: all 0.3s ease;
}

a:hover, a:focus {
  color: #ee8467;
}

a:focus {
  outline: none;
}

a[href^=tel] {
  color: #ffffff;
}

[role="button"] {
  cursor: pointer;
}

b, strong {
  font-weight: 700;
}

h1 {
  margin-top: 0;
  padding-top: 0;
  color: #58585a;
  font-weight: 700;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-justify {
  text-align: justify;
}

.text-bold {
  font-weight: 700;
}

.text-nowrap {
  white-space: nowrap;
}

.text-lowercase {
  text-transform: lowercase;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-capitalize {
  text-transform: capitalize;
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.list-unstyled {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.big-title {
  margin-bottom: 40px;
  font-size: 60px;
  display: inline-block;
}

.big-title::after {
  width: 35%;
  height: 3px;
  margin-top: 23px;
  background-color: #f69962;
  display: block;
  content: '';
}

.clearfix:after {
  display: table;
  clear: both;
  content: "";
}

/* forms */
.hide-placeholder::-webkit-input-placeholder {
  opacity: 1;
  color: #a1dfe4;
  transition: opacity 0.3s ease;
}

.hide-placeholder::-moz-placeholder {
  opacity: 1;
  color: #a1dfe4;
  transition: opacity 0.3s ease;
}

.hide-placeholder:-moz-placeholder {
  opacity: 1;
  color: #a1dfe4;
  transition: opacity 0.3s ease;
}

.hide-placeholder:-ms-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: #a1dfe4;
}

.hide-placeholder:focus::-webkit-input-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.hide-placeholder:focus::-moz-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.hide-placeholder:focus:-moz-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.hide-placeholder:focus:-ms-input-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.form-control {
  width: 100%;
  padding: 11px 20px;
  border: 1px solid #dbdcdc;
  font-size: 16px;
  display: block;
  background-color: #ffffff;
  transition: all .3s ease;
  outline: 1px solid #e5e4e2;
  outline-offset: -2px;
}

.form-control:not(select) {
  color: #616161;
}

.form-group {
  margin-bottom: 25px;
}

select {
  color: #a1dfe4;
}

.chose-select {
  color: #616161;
}

/* btn */
button:focus, button:active {
  outline: 0;
}

.btn {
  border: 0;
  background: transparent;
  text-align: center;
  cursor: pointer;
  line-height: 1;
  transition: all .3s ease;
}

.btn-transparent {
  font-size: 16px;
  font-weight: 700;
  padding: 9px 54px;
  color: #fff;
  border: 3px solid #f0a67f;
  display: inline-block;
}

.btn-transparent:hover {
  color: #f0a67f;
  border: 3px solid #fff;
}

.btn-rectangle {
  padding: 21px 33px;
  background-color: #f96059;
  color: #ffffff;
  font-size: 28px;
  display: inline-block;
}

.btn-rectangle:hover {
  color: #f96059;
  background-color: #ffffff;
}

/* top-panel */
.top-panel {
  padding-top: 9px;
  padding-bottom: 15px;
  background-color: #fff9f2;
  font-weight: 400;
  position: relative;
  z-index: 5;
  box-shadow: 0 1px 15px 0 rgba(42, 43, 43, 0.4);
}

/* navbar */
.navbar {
  padding-top: 35px;
}

.navbar-menu {
  font-size: 10.67px;
}

.navbar-menu a {
  color: #000;
  padding-bottom: 15px;
  display: inline-block;
  border-bottom: 2px solid transparent;
}

.navbar-menu .active a, .navbar-menu a:hover {
  border-bottom-color: #ffa679;
}

.navbar-toggle {
  display: none;
  padding: 10px 0 10px 10px;
  margin: 0;
  float: right;
  text-align: right;
}

/* header */
.header {
  overflow: hidden;
  position: relative;
  z-index: 2;
}

.header-title {
  padding: 6px 28px 4px;
  font-size: 10px;
  font-weight: 700;
  color: #fff;
  border: 3px solid transparent;
  display: inline-block;
  text-transform: uppercase;
  border-image: linear-gradient(to right, #ffa879 0%, #ffc373 100%);
  border-image-slice: 1;
}

.header-top {
  padding-top: 19px;
  padding-bottom: 23px;
}

.header-top .intro-text {
  padding-top: 10px;
  font-size: 25px;
  font-weight: 700;
  color: #fff;
  line-height: 1.4;
}

.header-bottom {
  font-size: 14px;
  line-height: 1.4;
  color: #fff;
  position: relative;
}

.header-bottom-text::after, .header-bottom-text::before {
  width: 0;
  height: 0;
  content: '';
  display: block;
  position: absolute;
  top: -21px;
}

.header-bottom-text::after {
  left: 98px;
  border: 19px solid transparent;
  border-top-width: 5px;
  border-bottom: 16px solid #f69962;
}

.header-bottom-text::before {
  left: 92px;
  border: 25px solid transparent;
  border-top-width: 0;
  border-bottom: 21px solid #fff;
}

.header-home {
  background: url(../img/example/main-img.jpg) no-repeat center top;
}

.header-home .header-bottom {
  padding-top: 20px;
  padding-bottom: 25px;
  background-color: #f69962;
}

.header--video-mag {
  background: url(../img/example/bg-video-mag.jpg) no-repeat center top;
}

.header--video-mag .header-top {
  padding-bottom: 46px;
}

.header--video-mag .intro-text {
  margin-right: -9px;
  margin-bottom: 40px;
}

.header--video-mag .header-bottom {
  padding-top: 24px;
  padding-bottom: 25px;
  background-color: #ebab3f;
}

.header--video-mag .header-bottom .stay {
  margin-top: 5px;
  color: #2d231d;
  font-size: 32px;
  font-weight: 700;
}

.header--video-mag .header-title {
  border-color: #e09f56;
}

.header--video-mag .header-bottom-text::after {
  border-bottom-color: #ebab3f;
}

.header--who-we-are {
  background: url(../img/example/bg-our-team.jpg) no-repeat center top;
}

.header--who-we-are .header-top {
  padding-top: 265px;
  padding-bottom: 45px;
}

.header--who-we-are .header-title {
  padding: 14px 45px;
}

.header--who-we-are-text {
  padding-top: 50px;
  color: #ffffff;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.3;
  font-family: 'Roboto', sans-serif;
  font-style: italic;
  text-align: justify;
}

.header--who-we-are .author {
  font-style: normal;
  color: #ff5238;
}

.header--digital-storytelling {
  background: url(../img/bg-digital.png) repeat-x 0 18% #00a8b3;
  box-shadow: 0 1px 15px 0 rgba(42, 43, 43, 0.4);
}

.header--digital-storytelling .text {
  margin-top: 30px;
  margin-bottom: 54px;
  border-radius: 28px;
  padding: 14px 20px 4px 29px;
  font-weight: 700;
  font-size: 50px;
  color: #00a9b9;
  background-color: #fff4e4;
  line-height: 60px;
  position: relative;
}

.header--digital-storytelling .text-allocation {
  color: #f69b61;
}

.header--digital-storytelling .text::before {
  border: 25px solid transparent;
  border-bottom-width: 0;
  content: '';
  position: absolute;
  border-top: 21px solid #fff4e4;
  bottom: -21px;
  left: 70px;
}

.header--digital-storytelling .header-top {
  padding-top: 45px;
  padding-bottom: 18px;
}

.header--digital-storytelling .header-bottom {
  padding-top: 20px;
  padding-bottom: 25px;
  background-color: #ffffff;
  color: #ffa679;
  letter-spacing: -.3px;
  box-shadow: 0 -3px 0 0 #00a8b3, 0 -5px 0 0 #f69962;
}

.header--digital-storytelling .header-bottom::before, .header--digital-storytelling .header-bottom::after {
  box-shadow: 0 -3px 0 0 #00a8b3, 0 -5px 0 0 #f69962;
}

.header--digital-storytelling .header-bottom-text::after {
  left: 82px;
  width: 56px;
  height: 18px;
  background: url(../img/arrow-decor.png) no-repeat center;
  border: 0;
  top: -21px;
}

.header--digital-storytelling .header-bottom-text::before {
  left: 78px;
  top: -31px;
  border: 32px solid transparent;
  border-top-width: 0;
  border-bottom: 32px solid #fff;
}

/* content */
.content {
  padding-top: 85px;
  /* .content-digital */
}

.content-home-list {
  overflow: hidden;
  text-align: center;
}

.content-home-list li {
  color: #ffeee6;
  position: relative;
  line-height: 1.4;
  float: right;
  overflow: hidden;
  font-size: 18px;
  font-weight: 600;
  text-align: left;
}

.content-home-list li:not(.fourth) {
  height: 210px;
}

@media (min-width: 961px) {
  .content-home-list li:not(.fifth):not(.eighth) {
    width: calc(100% / 4);
  }
}

@media (max-width: 960px) {
  .content-home-list li:not(.fifth):not(.eighth) {
    width: calc(100% / 3);
  }
}

.content-home-list li:not(.second):not(.fourth):not(.sixth):not(.eighth) {
  padding: 10px;
}

.content-home-list .first {
  background-color: #f59463;
}

.content-home-list .second {
  background: url(../img/example/img-2x.jpg) no-repeat center/cover;
}

.content-home-list .third {
  background-color: #fa6159;
}

.content-home-list .fourth {
  height: 420px;
  color: #fff;
  background: url(../img/img-2-2x-line.jpg) repeat-x top center/contain;
}

.content-home-list .fourth::after {
  width: 100%;
  height: 100%;
  background: url(../img/example/img-2-2x.jpg) no-repeat center/contain;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  z-index: 4;
}

.content-home-list .fourth span {
  width: 100%;
  height: 100%;
  padding: 15px;
  position: absolute;
  top: 0;
  left: 0;
  font-size: 33px;
  font-weight: 700;
  z-index: 5;
}

.content-home-list .fifth {
  float: left;
  padding: 10px;
  background-color: #de944f;
  font-size: 27px;
}

@media (min-width: 961px) {
  .content-home-list .fifth {
    width: calc(100% / 2);
  }
}

@media (max-width: 960px) {
  .content-home-list .fifth {
    width: 66.6666%;
  }
}

.content-home-list .sixth {
  background: url(../img/example/img-3-2x.jpg) no-repeat center/cover;
}

.content-home-list .seventh {
  background-color: #f0b95c;
}

.content-home-list .eighth {
  background: url(../img/example/img-4-2x.jpg) no-repeat center/cover;
}

@media (min-width: 961px) {
  .content-home-list .eighth {
    width: calc(100% / 2);
  }
}

@media (max-width: 960px) {
  .content-home-list .eighth {
    width: 66.6666%;
  }
}

.content-home-list .ninth {
  background-color: #de6b4f;
}

.content-digital .container-fluid {
  padding-top: 125px;
  background: url(../img/example/ex-img.jpg) no-repeat center top;
}

.content-digital .intro-text {
  max-width: 500px;
  width: 100%;
  padding-bottom: 30px;
  font-size: 30px;
  color: #fff;
  font-weight: 300;
  line-height: 37px;
}

.content-digital .intro-text::before {
  width: 35%;
  height: 3px;
  margin-bottom: 23px;
  background-color: #f69962;
  display: block;
  content: '';
}

.content-digital .big-title::after {
  background-color: #54e8f5;
}

.content-digital .info-title {
  margin-bottom: 70px;
  font-size: 26px;
  font-weight: 300;
  color: #a2a3a5;
}

.content-digital-list {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.content-digital-list li {
  width: calc(100% / 2);
  margin-bottom: 75px;
}

.content-digital-list li::before {
  min-height: 115px;
  content: '';
  display: block;
  margin: 0 auto;
}

.content-digital-list .icon-story::before {
  width: 117px;
  background: url(../img/icons/story.png) no-repeat center;
}

.content-digital-list .icon-content::before {
  width: 117px;
  background: url(../img/icons/content.png) no-repeat center;
}

.content-digital-list .icon-platform::before {
  width: 116px;
  background: url(../img/icons/platform.png) no-repeat center;
}

.content-digital-list .icon-tool::before {
  width: 153px;
  height: 124px;
  background: url(../img/icons/tools.png) no-repeat center;
}

.content-digital-list .icon-voice::before {
  width: 105px;
  background: url(../img/icons/voice.png) no-repeat center;
}

.content-digital-list .wrap {
  width: 153px;
  margin: auto;
  padding: 50px 0 0;
  color: #636365;
}

.content-digital-list .title {
  padding: 0 15px 23px;
  font-size: 22px;
  font-weight: 700;
}

.content-digital-list .title::after {
  width: 67%;
  height: 2px;
  margin: 17px auto 0;
  background-color: #54e8f5;
  display: block;
  content: '';
}

.content-digital-list .text {
  margin: 0;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.2;
}

/* footer */
footer {
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: #929292;
}

.btn-area {
  margin-bottom: 52px;
  text-align: center;
}

.btn-area .btn {
  font-size: 14px;
  padding: 10px 35px;
}

.btn-area .btn:not(:hover) {
  color: #fcc777;
}

.btn-area .btn:hover {
  color: #ffffff;
  background-color: #f0a67f;
  border-color: #f0a67f;
}

.copyright {
  font-size: 10px;
  color: #fff;
  text-align: center;
}

/* social */
.social {
  padding-bottom: 16px;
  border-bottom: 1px solid #ebe6e0;
}

.social li {
  display: inline-block;
  margin: 0 25px;
}

.social li:first-child {
  margin: 0;
}

.social li:first-child img {
  width: 75px;
  left: -9px;
  top: -18px;
}

.social li:last-child {
  margin: 0;
  width: 37px;
  left: 12px;
}

.social a {
  width: 60px;
  height: 60px;
  display: block;
  border-radius: 50%;
  border: 1px solid #ffc872;
  position: relative;
  overflow: hidden;
}

.social a:hover {
  border-color: #ffa679;
}

.social img {
  position: absolute;
  width: 35px;
  left: 11px;
  top: 12px;
  max-width: none;
}

/* footer-nav */
.footer-nav {
  padding-top: 26px;
  padding-bottom: 30px;
}

.footer-nav-memu {
  display: flex;
  justify-content: space-around;
  font-size: 10px;
  font-weight: 700;
}

.footer-nav-memu a {
  color: #fcc777;
  display: inline-block;
  padding-bottom: 15px;
  border-bottom: 2px solid transparent;
}

.footer-nav-memu a:hover {
  border-bottom-color: #fcc777;
}

/* contact-us */
.contact-us {
  padding-top: 50px;
  padding-bottom: 95px;
  background: #54e8f5;
  background: -moz-linear-gradient(-45deg, #54e8f5 0%, #00a8b5 100%);
  background: -webkit-linear-gradient(-45deg, #54e8f5 0%, #00a8b5 100%);
  background: linear-gradient(135deg, #54e8f5 0%, #00a8b5 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#54e8f5', endColorstr='#00a8b5', GradientType=1);
  position: relative;
}

.contact-us::before {
  width: 100%;
  height: 98%;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
  content: '';
  position: absolute;
  background: url(../img/bg-contacts.png) right top;
}

.contact-us .header-title {
  margin-bottom: 25px;
  padding-left: 44px;
  padding-right: 44px;
}

.contact-us .placeholder-list {
  margin-bottom: 40px;
}

.contact-us .placeholder-list li {
  width: 49%;
  margin-bottom: 7px;
  display: inline-block;
  vertical-align: top;
  color: #ffffff;
  font-size: 30px;
  font-weight: 300;
}

.contact-us .big-title {
  margin-bottom: 30px;
  color: #fff;
}

.contact-us-form {
  padding: 50px 35px;
  background-color: #fff;
}

.contact-us-form textarea {
  resize: none;
}

.contact-us-form .left-area {
  height: 480px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.contact-us-form .send-form {
  padding: 12px 10px 10px 20px;
  background-color: #ff7a6e;
  text-transform: uppercase;
  color: #fff;
  font-weight: 700;
}

.contact-us-form .send-form::after {
  width: 37px;
  height: 35px;
  margin-left: 18px;
  content: '';
  display: inline-block;
  vertical-align: middle;
  background: url(../img/icons/send.svg) no-repeat 100% center/contain;
}

.contact-us-form .send-form:hover {
  opacity: .8;
}

.contact-us-form .icon-fb, .contact-us-form .icon-ig {
  width: 43px;
  height: 42px;
  overflow: hidden;
  display: inline-block;
  vertical-align: bottom;
  position: relative;
}

.contact-us-form .icon-fb::before, .contact-us-form .icon-ig::before {
  content: '';
  width: 45px;
  height: 45px;
  position: absolute;
}

.contact-us-form .icon-fb:hover, .contact-us-form .icon-ig:hover {
  opacity: .8;
}

.contact-us-form .icon-fb {
  width: 43px;
  height: 42px;
  border: 2px solid #bcbdc0;
  border-radius: 10px;
}

.contact-us-form .icon-fb::before {
  width: 65px;
  height: 65px;
