.our-supporters-intro {
  margin-bottom: 70px;
  font-size: 26px;
  color: #ffffff;
  font-weight: 300;
  line-height: 1.1;
}

.our-supporters-map {
  padding-top: 450px;
  position: relative;
  min-height: 737px;
}

.our-supporters-map::after {
  width: 812px;
  height: 737px;
  background: url(../img/africa-map.png) no-repeat center/contain;
  content: '';
  position: absolute;
  top: 0;
  right: -142px;
}

.our-supporters-list {
  width: 540px;
